<template>
  <div class="container">
    <!-- 3.vuecomponentscount.vuestore.js{{$store.state.count}}count  -->
    <h4>{{msg}}</h4>
    <hr>
    <h3>{{count2}}</h3>
    <p>{{ doneTodos }}</p>
    <p>Completed Todos: {{ doneTodosCount }}</p>

    <!-- <router-link to="/vuex-actions">vuex 05 actions</router-link> -->
  </div>
</template>

<script>
  import store from "@/vuex/store"

  import { mapState, mapGetters } from 'vuex';

  console.log(store.getters.getTodoById(1))

  export default {
    name:'Getters',
    data() {
      return {
        msg: '04 getters'
      }
    },
    store,
    computed: {
      /* ...mapState(['count']),
      count () {
        return this.$store.getters.count;
      },*/      
      doneTodosCount () {
        return this.$store.getters.doneTodosCount
      },
      ...mapGetters(["count2", "doneTodos", "doneTodosCount", "getTodoById"])
    }
  }
</script>

<style lang="scss" scoped>
.container {
  width: 100%;
}

</style>



