<template>
  <div class="train-city">
    <h3>toCity{{sendData}}</h3> 
    <br/>
    <button @click='select(``)' style="color:blue;font-size:14px;text-decoration:underline;"></button>
  </div>
</template>
<script>
  export default {
    name:'trainCity',
    props:['sendData'], // 
    methods:{
      select(val) {
        let data = {
          cityname: val
        };
        this.$emit('showCityName',data);//selectshowCityName
      }
    }
  }
</script>


