<template>
 <div class="onbox">
   <!--  $oncallback -->
    <span>B, A->{{msg}}</span>
 </div>
</template>
<script>
	  import vmson from "../util/emptyVue"
	  export default {
		data(){
        return {
            msg:""
        }
    },
		mounted(){
        vmson.$on("aevent",(val)=>{//aevent;
            console.log(val);//a
            this.msg = val;
        })
    },
    beforeDestroy() {
      console.log('beforeDestroy');
      vmson.$off('aevent',this.msg)
    },
	}
</script>



