<template>
  <div class="container">
    <p> 1</p>
    <emitSon @my-event = "getMyEvent"></emitSon>
    <!-- my-event -->
    <br>
    <div>------------------------------------</div>
    <div>----------------------------------------------</div>
    <br>
    <!-- 2 -->
    <p> 2</p>
    <div class="eg02">
        <div>toCity{{toCity}}</div>
        <train-city @showCityName="updateCity" :sendData="toCity"></train-city>
    </div>

  </div>
</template>
<script>
  import emitSon from '../components/emitSon.vue'
  import trainCity from '../components/train-city.vue'  // 2
  export default {
    name:"emit-demo",
    data() {
      return {
        msg: "",
        toCity:"" // 2
      }
    },
    components: {
      emitSon,
      trainCity // 2
    },
    methods: {
      getMyEvent(msg){
        console.log('----------->' + msg) //---------> 
      },
      // 2
      updateCity(data){//-
        this.toCity = data.cityname;//
        console.log('toCity:'+this.toCity)
      }
    }
  }
</script>



