// mockjs
// import Mock from 'mockjs';
const Mock = require("mockjs");

//  mock.Random 
const Random = Mock.Random;

// 
var arr = [];
for (let i = 0; i < 5; i++) {
    let newArticleObject = {
        name: Random.cname(),
        content: Random.csentence(5, 10),
        id: i
    }
    arr.push(newArticleObject); //
}

// 
let list = function(options) {
    // console.log(options.type);
    let rtype = options.type.toLowerCase(); //
    switch (rtype) {
        case 'get':
            break;
        case 'post':
            let id = parseInt(JSON.parse(options.body).params.id); // idoptions.bodyJSON
            arr = arr.filter(function(val) {
                return val.id != id; // id
            });
            break;
        default:
            break;
    }
    return {
        data: arr
    }
}
Mock.mock('/list', /get|post/i, list);

// 
let listAdd = function(options) {
    //  console.log(""+JSON.parse(options.body).params.obj);
    let rtype = options.type.toLowerCase(); //
    switch (rtype) {
        case 'get':
            break;
        case 'post':
            let obj = JSON.parse(options.body).params.obj;
            // console.log(""+ obj);
            arr = arr.concat(obj); // 
            // reduce() es6
            // arr = arr.reduce(function () {
            //   return arr.concat(obj); // .concat()
            // })
            break;
        default:
            break;
    }
    return {
        data: arr
    }
}
Mock.mock('/listAdd', /get|post/i, listAdd);

// 
let listUpdate = function(options) {
    let rtype = options.type.toLowerCase(); //
    switch (rtype) {
        case 'get':
            break;
        case 'post':
            let obj = JSON.parse(options.body).params.obj;
            // console.log(JSON.parse(options.body).params);
            // let id = parseInt(JSON.parse(options.body).params.obj.id);
            arr = arr.map(val => { // 
                return val.id === obj.id ? obj : val;
            });
            break;
        default:
            break;
    }
    return {
        data: arr
    }
}
Mock.mock('/listUpdate', /get|post/i, listUpdate);

// 
let listSearch = function(options) {
    // console.log(options.type);
    let rtype = options.type.toLowerCase(); //
    switch (rtype) {
        case 'get':
            break;
        case 'post':
            let id = parseInt(JSON.parse(options.body).params.id); // idoptions.bodyJSON
            arr = arr.filter(function(val) {
                return val.id == id; //true
            });
            break;
        default:
            break;
    }
    return {
        data: arr
    }
}
Mock.mock('/listSearch', /get|post/i, listSearch);




// -------------------------------------------------------
// mock
const produceNewsData = function() {
    let articles = [];
    for (let i = 0; i < 10; i++) {
        let newArticleObject = {
            arcId: Random.guid(),
            title: Random.csentence(5, 30), //  Random.csentence( min, max )
            thumbnail_pic_s: Random.dataImage('100x150', 'mock'), // Random.dataImage( size, text )  Base64 
            author_name: Random.cname(), // Random.cname() 
            date: Random.date() + ' ' + Random.time() // Random.date(),yyyy-MM-ddRandom.time() 
        }
        articles.push(newArticleObject)
    }

    return {
        articles: articles
    }
}

// Mock.mock( url, post/get , )
Mock.mock('/news/index', 'post', produceNewsData);

// mock 
const produceDetailData = function() {
    var detailData = {
        picture: Random.dataImage("750x375", ""),
        title: Random.csentence(5, 30),
        thumbnail_pic_s: Random.dataImage('100x150', 'mock'),
        author_name: Random.cname(),
        publishDate: Random.date("yyyy/MM/dd ") + Random.time("HH:mm"),
        details: Random.csentence(40, 160)
    };

    var news = produceNewsData();
    let {
        projectInformation
    } = news;
    return {
        detailData,
        projectInformation
    };
};

Mock.mock('/news/detail', 'post', produceDetailData);


