<template>
  <div class="container">
    <!-- https://blog.csdn.net/weixin_42695446/article/details/84672633 -->
    <div>{{a}}</div>
    <a href="javascript:void(0);" v-on:click="a='hello!'"></a>
    <br>
    <!-- <div v-on:click="alert"></div> -->
    <div @click="click"></div>

    <br>
    <hr>
    <br>
    <!-- props -->
    <proptest :title-one="abc"></proptest>  
  </div>
</template>

<script>
  import proptest from '../../components/proptest.vue'
  export default {
    name:'mdemo',
    data (){
      return {
        a:"",
        abc: {
          'one':1,
          'two':2
        }
      }
    },
    components:{
      proptest
    },
    methods:{
      init: function () {
          console.log(this.a);
      },
      click:function(){
          this.alert();
      },
      alert:function(){
          alert(this.a);
      }
    },
    mounted: function () {
        this.init();
        //
    }
  }
</script>

<style scoped>
.container div {
  padding:10px 0;
}
</style>



