<template>
  <div class="onbox">
    <!--  $emit  -->
    <span>A->{{msg}}</span>
    <br>
    <br>
    <input type="button" value="ab" @click="send">
  </div>
</template>

<script>
  import vmson from "../util/emptyVue"
  export default {
    data(){
      return {
        msg:{
          a:'111',
          b:'222'
        }
      }
    },
    methods:{
      send(){
        vmson.$emit('aevent',this.msg);
      }
    }
  }
</script>


