<template>
  <div class="container">
    <!--  -->
    <test-1></test-1>
    <test-2></test-2>
  </div>
</template>

<script>

  import Vue from 'vue'

  var bus = new Vue();
  Vue.component('test-1', {
      template: `<div @click="handle">test-1</div>`,
      methods: {
          handle: function () {
              bus.$emit("msg", "test-1")
          }
      }
  });
  Vue.component('test-2', {
      template: `<div>message{{message}}</div>`,
      mounted: function () {
          /* var _this = this;
          bus.$on("msg", function (value) {
              _this.message = value;
          }) */

          /* test-2busthisthisbusVuetest-2
          thistest-2 */
          bus.$on("msg",  (value) =>{
              this.message = value;
          })
      },
      data: function () {
          return {
              message: ""
          }
      },
  });
  var app = new Vue({
      el: "#app",
  })
</script>



