<template>
  <div class="container">
    <!-- <br> -->
    <button @click="emitEvent">console</button>
    <br>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        msg: ""
      }
    },
    methods: {
      emitEvent() {
        //$emitmy-eventthis.msg
        this.$emit('my-event', this.msg)
      }
    }
  }
</script>


