<template>
  <div class="container">
    <!-- https://www.jianshu.com/p/8e705bb0bb4b -->
    
    <!--  -->
    <!--  -->
    <br>
    <div>abc{{abc}}</div>
    <br>
    <!--1addaddabc1 -->
    <!-- <tx-test @change1="add"></tx-test> -->

    <!--2
     $eventadd -->
    <!-- <tx-test @change1="add($event)"></tx-test> -->
    
    <!-- 3.native -->
    <tx-test @click.native="add"></tx-test>
  </div>
</template>

<script>
import txtest from "../../components/txtest"
  export default {
    name:"componentTongxin",
    data(){
      return {
         abc:0
      }
    },
    components: {
      'tx-test':txtest
    },
    methods: {
      // (1)(3)
      add(){
        this.abc++
      },
      // 2
      /* add(value){
        this.abc+=value
      } */
    }
  }
</script>



