<template>
  <div class="container">
    <!-- https://www.jianshu.com/p/8e705bb0bb4b -->
    <!--   -->
     <this-input :msg="parentMsg" v-on:message="receiveMsg"></this-input>
     <this-dialog></this-dialog>
  </div>
</template>

<script>
  import thisInput from '../../components/input'
  import thisDialog from '../../components/dialog'

  import Vue from 'vue'

  export default {
    data(){
      return {
        parentMsg:[1,2,3],
        flag:false,
        Bus:new Vue({})  //  Vue 
      }
    },
    components: {
      thisInput,
      thisDialog
    },
    methods:{
      receiveMsg(val){
        this.flag=val;
      }
    }
  }
</script>

<!--
 Bus
 eventName

 Bus this.$root.$onthis.$root.$emit 
 Vue Bus
-->


