<template>
  <div class="container">
    <!-- https://blog.csdn.net/weixin_42695446/article/details/84672633 -->
    <div>{{message}}</div>
    <div>righttruemessage10100</div>
    <div>{{judge}}</div>

    <br>

    <div>{{total}}</div>
    <div v-on:click="add1">data11:{{data1}}</div>
    <div v-on:click="add2">data21:{{data2}}</div>

    <br>
    <div>{{total2}}</div>
  </div>
</template>

<script>
  export default {
    name: 'cdemo',
    data() {
        return {
          message: 1,
          right: true,
          data1: 1,
          data2: 2,
          first: "a",
          last: "b"
        }
    },
    computed: {
        judge: function () {
            if (this.right) {
                return this.message + 10;
            }
            else {
                return this.message + 100;
            }
        },
        total: function () {
            return this.data1 + this.data2
        },
        total2: {
          get:function(){
              return this.first +" "+ this.last
          },
          set:function(value){
              var names=value.split(" ");
              this.first=names[0];
              this.last=names[1];
          }
        }
    },
    methods: {
      add1: function () {
          this.data1++;
      },
      add2: function () {
          this.data2++;
      }
    }
  }

  /** 

  ,

  
messagejudge

 */
</script>

<style scoped>
.container div {
  padding:10px 0;
}
</style>



