<template>
  <!-- v-cloak https://www.jianshu.com/p/f56cde007210?utm_source=oschina-app -->
  <!--  Vue.js  Vue  Vue  v-cloak  -->
  <!--  v-cloak webpackvue-router div  v-cloak  -->
  <div class="container" v-cloak>
    <!-- https://www.cnblogs.com/divtab/p/10940770.html -->
    <div>{{date}}</div>
    <br>
    <div>
      {{date | fmtDate}}
    </div>
  </div>
</template>
<script>
  import axios from "axios"
  import moment from "moment"
  export default {
    name:'one02',
    data(){
      return {
        date: new Date()
      }
    },
    filters: {
      fmtDate(v){
        return moment(v).format('YYYY-MM-DD hh:mm:ss');
      }
    }
  }
</script>

<style lang="scss" scoped>
[v-cloak]{
  display: none !important;
}
.container {
  margin:0 10px;
  display:flex;
  flex-direction: column;
  justify-items: center;
  align-items: middle;
  text-align: left;
}
</style>


