<template>
  <div class="container">
    <!-- 1(2) $emit 
    v-on:click="$emit(change1)"<div>change1v-on<test>
    -->
    <!-- <div class="bluehand" @click="clickSendMeg"></div> -->

    <!-- 3.1$emitclickclick -->
    <!-- <div class="bluehand" @click="clickSendMeg0"></div>  -->
    <!-- 3.2DOM.native -->
    <div class="bluehand"></div>
  </div>
</template>

<script>
  export default {
    name:'tx-test',
    data(){
      return {

      }
    },
    methods: {
       clickSendMeg() {
         this.$emit('change1',5)
       },
       clickSendMeg0() {
         this.$emit('click')
       }
    }
  }
</script>

<style scoped>
.bluehand {
  color:#177bbb;
  cursor:pointer;
  padding:5px;
  border:1px solid #177bbb;
}
</style>




