<template>
  <div class="container">
    <!-- 1. -->
    <!-- 2refAthis.$refs -->
    <test-1 ref="A"></test-1>
    <div>message: {{message}}</div>
    
    <br>
    <hr>
    <!-- 2. -->
    <div v-on:click="handle2">message2: {{msg2}}</div>
    <!-- 2this.$children
Vueref -->
    <!-- <test-1 ref="A"></test-1> -->
  </div>
</template>

<script>
import test1 from "../../components/test1"
export default {
    name: 'bus2',
    data(){
      return {
        message: "",
        msg2: ""
      }
    },
    components: {
      "test-1":test1
    },
    methods: {
      // 2. 
      handle2: function() {
          /* var a = this.$children[0].message2; */
          var a = this.$refs.A.message2;
          this.msg2 = a;
      }
    }
  }
</script>



