<template>
  <div class="refbox">
    <!-- 
      $refs
      
      -
     -->
     <child-a ref="child"></child-a>
     <!-- ref -->
     <button @click="getMyEvent"></button>
  </div>
</template>

<script>
  import ChildA from '../components/ref-child.vue'
  export default {
    name:'ref-demo',
    data(){
      return {
        msg:""
      }
    },
    components:{
      'child-a':ChildA
    },
    methods:{
      getMyEvent(){
        this.$refs.child.emitEvent(this.msg);
        //childrefemitEvent
      }
    }
  }
</script>


