<template>
  <div class="container">
    <h4>{{msg}}</h4>
    <hr>
    <h5>{{count}}</h5>
    <!-- <h5>+localCount{{countPlusLocalState}}</h5> -->
    <p> {{ localComputed }}</p>

    <!-- <router-link to="/vuex-mutations">vuex 03 Mutations</router-link> -->
  </div>
</template>

<script>
  import store from "@/vuex/store"
  import {mapState} from 'vuex'
  export default {
    name:'State',
    data() {
      return {
        msg: '02 state',
        localCount: 4
      }
    },
    store,
    // computed
    /* computed:{
      count(){
          return this.$store.state.count;
      }
    } */
    // mapState
    /* computed:mapState({
      count: state => state.count,
      countAlias: 'count',
      countPlusLocalState (state) {
        return state.count + this.localCount
      }

    }) */
    // mapState
    // 
    // computed: mapState(['count'])
    computed: {
      localComputed () {
        return this.count + 10;
      },
      // 
      ...mapState({
        count: 'count'
      })
    }

  }


  
</script>

<style lang="scss" scoped>
.container {
  width: 100%;
}

</style>



