<template>
  <div class="container">
      <!-- <button class="bluehand" @click="handle">+1</button> -->

      <!--  -->
      <input type="text" :value="value" @input="update">
  </div>
</template>

<script>
  export default {
    name: "abc",
    props:["value"], // v-model  valuecheckedselected  Vue  valuetotal
    data(){
      return {
        count: 0
      }
    },
    methods: {
      handle:function(){
        this.count++;
        this.$emit("input",this.count)
      },
      update(e){
        this.$emit("input",e.target.value);
      }
    }

  }
</script>

<style lang="" scoped>
  
</style>


