<template>
  <div class="container">
    <!-- 3.vuecomponentscount.vuestore.js{{$store.state.count}}count  -->
    <h4>{{msg}}</h4>
    <hr>
    <h5>{{$store.state.count}}</h5>
    <!-- 5.count.vuemutations -->
    <div>
      <button @click="$store.commit('add')">+</button>
      <button @click="$store.commit('reduce')">-</button>
    </div>

    <!-- <router-link to="/vuex-count">vuex 01</router-link> -->
  </div>
</template>

<script>
  import store from "@/vuex/store"
  export default {
    name:'Count',
    data() {
      return {
        msg: '06 module'
      }
    },
    store
  }
</script>

<style lang="scss" scoped>
.container {
  width: 100%;
}

</style>



